<template>
  <div class="publishAsk">
    <mt-header title="发布问题">
      <mt-button  icon="back" slot="left" @click="handleBack" class="headerBtn">返回</mt-button>
      <mt-button  slot="right" @click="postAsk()"  class="headerBtn">提交</mt-button>
    </mt-header>
    <div class="expert" v-if="expert">
      <span>向专家</span>
      <img :src="getUserAvatar">
      <h3>{{expert.name}}</h3>
      <span>提问</span>
    </div>
    <section class="ask">
      <input placeholder="标题(必填)" v-model="askParams.title"/>
      <textarea placeholder="您的问题" v-model="askParams.nr">
      </textarea>
    </section>
    <!-- 10线条 -->
    <div class="lise_10"></div>

    <div class="askType">
      <span>问题类别:</span>
      <select v-model="askParams.lb">
        <option value ="0">选择类别</option>
        <option v-for="item in lbArr" :value="item.ask_type_id" >{{item.name}}</option>
      </select>
    </div>

    <!-- 10线条 -->
    <div class="lise_10"></div>

    <div class="upImg">
      <div class="imgWrap">
        <div class="img" v-for="item,i in askParams.img" v-if="askParams.img.length">
          <img :src="item" @click="showQImg(item)">
          <i @click="delUpImg(i)"><img src="../../images/main/img_del.png"></i>
        </div>
        <div class="img" v-if="askParams.img.length == 0" style="width:30px;">
          <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                <img src="../../images/main/img_icon.png">
          </upload>
        </div>
        <div class="img" v-if="askParams.img.length < 3 && askParams.img.length > 0">
          <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                <img src="../../images/main/add_img.png">
          </upload>
        </div>

      </div>
      <p v-if="askParams.img.length == 0">一共可以选择3张图片</p>
      <p v-if="askParams.img.length">已选{{askParams.img.length}}张，还可选择{{3 - askParams.img.length}}张</p>
    </div>

    <section class="PopImg" v-if="isShowPopImg" @click="showPopImg(0)">
      <div><img :src="popImgUrl"></div>
    </section>
    <i class="publishAsk_btn" @click="postAsk()">提交</i>
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl,getUserAvatar} from '../../components/common/mUtils'
import upload from '../../components/common/upload'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userID:'',
      userToken:null,
      data:[],
      isShowTool:false,
      askParams:{
        title:'',
        nr:'',
        lb:0,
        imgs:'',
        img:[],
        fen:0,
        y_time:0,
        zj:0,
      },
      popImgUrl:'',
      zjid:0,
      expert:null,
      isShowPopImg:false,
      lbArr:[
          {ask_type_id:1,name:'新房',paixu:0,ifask:1,ifbaike:1},
          {ask_type_id:2,name:'二手房',paixu:0,ifask:1,ifbaike:1},
          {ask_type_id:3,name:'评估',paixu:0,ifask:1,ifbaike:1},
          {ask_type_id:4,name:'法律',paixu:0,ifask:1,ifbaike:0}]
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  computed:{
    getUserAvatar:function (){
      return getUserAvatar(this.expert.avatarPath)
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')

    this.zjid = this.$route.query.zjid;
    if (this.userID) {
      this.isLogin = true
    }
  },
  mounted(){
    //this.userToken = 'CYKVUJ8teDegtOaHabyCwvYIHsfYRLh0'
    this.getAskType()

    if (this.isLogin && this.zjid) {
      this.askParams.zj = this.zjid
      // 获取专家资料
      this.getExpert()
    }

  },
  components:{
    upload,
  },
  methods: {
      handleBack(){
        this.$router.go(-1);
      },
      showTool(){
        this.isShowTool = !this.isShowTool
      },
      // 获取问题类别
      getAskType(){
        Indicator.open();
        let getUrl = this.CFWConfig.APIHost + '/v3/ask/lb'
        // console.log(getUrl)
        this.$http.get(getUrl).then(response => {
          let code = response.data.code
          if (code == 200) {
            this.lbArr = response.data.result
          }
          Indicator.close();
        }, response => {
          Toast('请求失败');
          console.log(response);
          Indicator.close();
        });
      },
      // 获取专家资料
      getExpert(){
        let urlStr = ''
        if (this.isLogin) {
          urlStr = '&uid='+this.userID
        }
        let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjdetail?id='+this.zjid+urlStr
        // console.log(getUrl)
        this.$http.get(getUrl).then(response => {
          let code = response.data.code
          if (code == 200) {
            this.expert = response.data.result
          }
          Indicator.close();
        }, response => {
          Toast('请求失败');
          console.log(response);
        });
      },
      postAsk(){
        if (!this.validateFormData()) {
          return
        }
        Indicator.open('提交中...');
        let imgsUrls = ''

        this.askParams.img.forEach((item) =>{
          imgsUrls = imgsUrls == '' ? item : imgsUrls+','+item
        })
        let params = {
          'title' : this.askParams.title,
          'nr':this.askParams.nr,
          'lb':this.askParams.lb,
          'imgs':imgsUrls,
        }

        // 发起请求
        let getUrl = this.CFWConfig.APIHost + '/v3/ask/tiwen?token='+this.userToken
        this.axios.post(getUrl,this.objToformData(params))
        .then(function (response) {
          let code = response.data.code
          if (code == 200) {
            Toast(response.data.message);
          }
          setTimeout(() => {
            this.handleBack()
          }, 1500);
          Indicator.close();
        }.bind(this))
        .catch(function (error) {
          console.log(error);
        });

      },
      // 表单数据验证
      validateFormData(){
        if (this.askParams.title == '') {
          Toast('请输入问题标题！');
          return
        }

        // if (this.askParams.nr == '') {
        //   Toast('请输入描述！');
        //   return
        // }

        if (this.askParams.lb == 0) {
          Toast('请选择问题类别！');
          return
        }
        return true
      },
      // 参数转化成表单对象
      objToformData(obj){
        // let params = new URLSearchParams()
        let params = new FormData()
        Object.keys(obj).forEach( (key, i) => {
          params.append(key, obj[key]);
        });
        return params;
      },
      // 上传图片过程中的回调
      uploadProgress (progress, flag) {
          //这里可以通过回调的flag对不同上传域做处理
          this.progress = progress < 100 ? progress : 0;
          console.log(this.progress)
      },
      // 上传图片完成的回调
      uploadComplete(status, result, flag) {
          if (status == 200) { //
            let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
            this.askParams.img.push(imgUrl)
          } else {
              //失败处理
          }
      },
      // 显示隐藏 PopImg
      showPopImg(isShow){
        this.isShowPopImg = isShow
      },
      showQImg(item){
        this.isShowPopImg = 1
        this.popImgUrl = item
      },
      delUpImg(idx){
        let array = []
        this.askParams.img.forEach((item, i) =>{
          if (idx != i) {
            array.push(item)
          }
        })
        this.askParams.img = array
      }
  }

}

</script>

<style scoped>
.publishAsk{

}

.publishAsk .headerBtn{
  color: #008fff;
}

.publishAsk .ask{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.publishAsk .ask input{
  padding: 5px;
  font-size: 15px;
  border-bottom: 1px solid #eee;
  height: 36px;
  -webkit-appearance: none;
}

.publishAsk .ask textarea{
  height: 120px;
  font-size: 15px;
  padding: 5px;
  -webkit-appearance: none;
  border: none;
}

.publishAsk .askType{
  height: 40px;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  padding: 0 10px;
}

.publishAsk .askType select{
  font-size: 15px;
  border: none;
  flex-grow: 1;
  background-color: none;
}


.upImg{
  height: 200px;
  background-color: #fff;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.upImg .imgWrap{
   display: flex;
   flex-direction: row;
   align-items: center;
   max-width: 480px;
}

.upImg .imgWrap div{
  width: calc(100%/3 - 6px);
  margin-right: 10px;

}

.upImg .imgWrap img{
  width: 100%;
  max-height: 130px;
  /*height: 100%;*/
}
.upImg .imgWrap .img{position: relative;}
.upImg .imgWrap .img i{
  position: absolute;
  top: 0;
  right: 0;
  width: 18px;
  height: 18px;
  color: #fff;
}

.upImg p{
  margin-top: 10px;
}

/*图片游览*/
.PopImg{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100vh);
  background-color: rgba(0, 0, 0, 1);
}
.PopImg div{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.PopImg img{
  width: calc(100% - 20px);
  padding: 10px;
}

.publishAsk_btn{
  position: fixed;
  right: 0px;
  bottom: 0px;
  background-color: #31a4fc;
  width: 100%;
  height: 45px;
  text-align: center;
  line-height: 45px;
  font-size: 16px;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  /*border-radius: 29px;*/
  /*border: 4px solid rgba(255,255,255,0.8);*/
  /*border: 4px solid rgba(255,255,255,1);*/
}

.publishAsk .expert{
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid #eee;
}

.publishAsk .expert span{
  font-size: 16px;
  font-weight: bold;
  color: #000;
  margin-left: 10px;
}

.publishAsk .expert h3{
  font-size: 15px;
  font-weight: bold;
  color: #999;
  margin-left: 2px;
}

.publishAsk .expert img{
  width: 28px;
  height: 28px;
  border-radius: 14px;
  margin-left: 10px;
}


</style>



